
import { graphql, Link, PageProps } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import moment from 'moment';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import Icon from '../../components/Icon';
import Layout from '../../components/Layout';
import Role from '../../components/Role';
import SEO from '../../components/SEO';
import Tab from '../../components/Tab';
import { ImageFile, getProjectImageData, parseProject } from '../../tools/project';

type DataProps = {
	project: {
		fields: {
			contents: string,
		}
		relativeDirectory: string,
	}
	images: {
		nodes: Array<ImageFile>
	}
	icons: {
		nodes: Array<{
			name: string,
		}>
	}
}

const ProjectTemplate: FC<PageProps<DataProps>> = ({ data }) => {
	const project = parseProject(data.project.fields.contents, data.project.relativeDirectory);
	const parseTimeText = (time: string): string => moment(time).format('MMM YYYY');
	const getDuration = (time: { start: string, end: string | null}): string => {
		const endTime = (
			time.end ?
				moment(time.end).endOf('month') :
				moment()
		);
		const duration = moment.duration(endTime.diff(moment(time.start)));
		const year = Math.floor(duration.asYears());
		const month = Math.round(duration.asMonths() % 12);
		return `${(year > 0) ? `${year}yrs ` : ''}${month}mos`;
	}

	return (
		<Layout>
			<div
				className={style.div}
			>
				<section
					className={style.header}
				>
					<div>
						<GatsbyImage
							className={style.cover}
							image={getProjectImageData(
								project.pathname,
								project.image.cover,
								data.images.nodes
							)}
							alt={`${project.name} cover`}
							loading='lazy'
						/>
						{project.image.icon?
							<GatsbyImage
								className={style.icon}
								image={getProjectImageData(
									project.pathname,
									project.image.icon,
									data.images.nodes
								)}
								alt={`${project.name} icon`}
								loading='eager'
							/>:
							<></>
						}
					</div>
					<main>
						<h1>{project.name}</h1>
						<h2>{project.headline}</h2>
					</main>
				</section>

				<section
					className={style.navigator}
				>
					<a
						className={project.link.code ? '' : style.disable}
						href={project.link.code}
						target='_blank'
						rel='noreferrer'
						title='source code link'
					>CODE</a>
					<div
						className={style.verticalLine}
					/>
					<a
						className={project.link.demo ? '' : style.disable}
						href={project.link.demo}
						target='_blank'
						rel='noreferrer'
						title='DEMO link'
					>DEMO</a>
				</section>
				<section
					className={style.textCenter}
				>
					{`${parseTimeText(project.time.start)}` +
						` - ${project.time.end ? parseTimeText(project.time.end) : 'Present'}` +
						` · ${getDuration(project.time)}`
					}
				</section>
				<section
					className={style.tabs}
				>
					<h1>Tab</h1>
					<div>
						{project.tabs.map(tab =>
							<Tab
								key={`tab_${tab}`}
								name={tab}
							/>	
						)}
					</div>
				</section>
				<section
					className={style.roles}
				>
					<h1>Role</h1>
					<div>
						{project.roles.map(role =>
							<Role
								key={`role${role}`}
								name={role}
							/>		
						)}
					</div>
				</section>
				<section
					className={style.programming}
				>
					<h1>Techniques <span>|</span> Frameworks <span>|</span> Languages</h1>
					<div>
						<div
							className={style.icons}
						>
							{project.programming.techniques.map(technique =>
								<Link
									key={`icon_${technique}`}
									to={`/projects/?techniques=${technique}`}
								>
									<Icon
										name={technique}
									/>
								</Link>
							)}
						</div>
						<div
							className={style.verticalLine}
						/>
						<div
							className={style.icons}
						>
							{project.programming.frameworks.map(framework =>
								<Link
									key={`icon_${framework}`}
									to={`/projects/?frameworks=${framework}`}
								>
									<Icon
										name={framework}
									/>
								</Link>
							)}
						</div>
						<div
							className={style.verticalLine}
						/>
						<div
							className={style.icons}
						>
							{project.programming.languages.map(language =>
								<Link
									key={`icon_${language}`}
									to={`/projects/?languages=${language}`}
								>
									<Icon
										name={language}
									/>
								</Link>
							)}
						</div>
					</div>
				</section>
				
				<section
					className={style.description}
				>
					{project.descriptions.map(description =>
						<article
							key={`article_${description.title}`}
						>
							<div>
								<h1>{description.title}</h1>
								<div
									className={style.content}
								>
									{description.text ?
										<p>{description.text}</p> :
										<></>
									}
									{description.list ?
										<ul>
											{description.list.map((text, index) =>
												<li
													key={`descriptionText_${description.title}_${index}`}
												>
													{text}
												</li>
											)}
										</ul> :
										<></>
									}
								</div>
							</div>
							{description.image?
								<GatsbyImage
									className={style.image}
									image={getProjectImageData(
										project.pathname,
										description.image,
										data.images.nodes
									)}
									alt={`${description.title} image`}
									loading='lazy'
								/>:
								<></>
							}
						</article>
					)}
				</section>
			</div>
		</Layout>
	)
}
export default ProjectTemplate;

export const Head: FC<PageProps<DataProps>> = ({ data, location }) => (
	<SEO
		title={`${parseProject(data.project.fields.contents).name} | Projects`}
		pathname={location.pathname}
	/>
)

export const query = graphql`
    query Project($id: String!){
		project: file(id: {eq: $id}) {
			fields {
				contents
			}
			relativeDirectory
		}
		images: allFile(
			filter: {
				extension: {regex: "/png|jpg/"},
				relativeDirectory: {regex: "/^projects//"}
			}
		) {
			nodes {
				childImageSharp {
					gatsbyImageData
				}
				name
				ext
				relativeDirectory
			}
		}
		icons: allFile(
			filter: {relativeDirectory: {eq: "icon"}}
		) {
			nodes {
				name
				childImageSharp {
					gatsbyImageData
				}
			}
		}
	}
`
